import {mockAuth} from "../_api/mock-server";
import {BAD_CREDENTIALS, SERVER_ERROR} from "../_constants";

export const authService = {
  sendUsername(username) {
    return mockAuth.sendPassword(username).catch(e => {
      if (e.message === BAD_CREDENTIALS) throw new Error(BAD_CREDENTIALS);
      else throw new Error(SERVER_ERROR)
    });
  },
  resendPassword(username) {
    return mockAuth.resendPassword(username).catch(e => {
      if (e.message === BAD_CREDENTIALS) throw new Error(BAD_CREDENTIALS);
      else throw new Error(SERVER_ERROR)
    });
  },
};
